@import'hero';
@import'./components/infos_bar';
@import'./components/button';
@import'event';
@import'./components/variables';
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: Roboto, sans-serif;
    color: $textColor;
    list-style:none;
}

.container{
    max-width: 960px;
    width: 100%;
    margin: 0 auto;

    @media(max-width: 640px){
        max-width: 80%;
    }
    @media(max-width:1024px){
        max-width: 90%;
    }
}
